
import Drawer from 'vux/src/components/drawer/index.vue'

export default {
  data() {
    return {
      placement: 'left',
      showMode: 'push',
      drawerShow: false,
      name: 'slide-left', // 默认动态路由变化为slide-right
      Comname : '综合商城',
      navlist : [
        {
          path: '/',
          name: '首页',
          icon: require('./assets/home.png')
        },
        {
          path: '/list_classify',
          name: '产品分类',
          icon: require('./assets/009_ov8h.png')
        },
        {
          path: '/list_groupprice?list=groupprice',
          name: '精品团购',
          icon: require('./assets/010_hrtj.png')
        },
        {
          path: '/list_groupprice?list=seckill',
          name: '限时秒杀',
          icon: require('./assets/007_gsn8.png')
        },
        {
          path: '/list_article',
          name: '帮助中心',
          icon: require('./assets/005_r27e.png')
        },
        {
          path: '/list_experience',
          name: '体验心得',
          icon: require('./assets/002_43hh.png')
        },
      ],
      copyRight: '2012-0005© 2003-2016 xxxxx.COM 版权所有',
      tel: '400-000-0000',
      qq: require('./assets/qq_1.png'),
      toolbar: [
        {
          icon: require('./assets/home_uuqd.png'),
          title: '首页',
          link: '#/'
        },
        {
          icon: require('./assets/list_u05t.png'),
          title: '分类',
          link: '#/list_classify'
        },
        {
          icon: require('./assets/shop.png'),
          title: '购物车',
          link: '#/list_cart'
        },
        {
          icon: require('./assets/user.png'),
          title: '我的',
          link: '#/list_user'
        },
      ]
    }
  },
  methods: {
    drawerToggle() {
      this.drawerShow = !this.drawerShow
    },
    onHide() {
      let wrap = this.$el.firstChild.firstChild;
      if(this.placement == 'left') {
        wrap.classList.remove('actives');
      } else if(this.placement == 'right') {
        wrap.classList.remove('activer');
      }
    },
    changeDrawerShow(state) {
      this.drawerShow = state;
    },
    onShow() {
      let wrap = this.$el.firstChild.firstChild;
      if(this.placement == 'left') {
        wrap.classList.add('actives');
      } else if(this.placement == 'right') {
        wrap.classList.add('activer');
      }
    }
  },
  watch: {        //监听路由切换
    $route() {
      this.drawerShow = false;
    }
  },
  components: {
    Drawer
  },
}



