import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'index',
      component:resolve => require(['@/pages/index'],resolve),  
    },
    {
      path: '/list_classify',
      name: 'list_classify',
      component:resolve => require(['@/pages/list_classify'],resolve),  
    },
    {
      path: '/list_product',
      name: 'list_product',
      component:resolve => require(['@/pages/list_product'],resolve),  
    },
    {
      path: '/list_groupprice',
      name: 'list_groupprice',
      component:resolve => require(['@/pages/list_groupprice'],resolve),  
    },
    {
      path: '/list_article',
      name: 'list_article',
      component:resolve => require(['@/pages/list_article'],resolve),  
    },
    {
      path: '/msg_article',
      name: 'msg_article',
      component:resolve => require(['@/pages/msg_article'],resolve),  
    },
    {
      path: '/list_experience',
      name: 'list_experience',
      component:resolve => require(['@/pages/list_experience'],resolve),  
    },
    {
      path: '/list_cart',
      name: 'list_cart',
      component:resolve => require(['@/pages/list_cart'],resolve),  
    },
    {
      path: '/list_user',
      name: 'list_user',
      component:resolve => require(['@/pages/list_user'],resolve),  
    },
    {
      path: '/msg_product',
      name: 'msg_product',
      component:resolve => require(['@/pages/msg_product'],resolve),  
    },
    {
      path: '/msg_groupprice',
      name: 'msg_groupprice',
      component:resolve => require(['@/pages/msg_groupprice'],resolve),  
    },
  ]
});



